// components/NavBar.jsx

import React from 'react';
import {
  Box,
  Flex,
  Heading,
  Spacer,
  IconButton,
  useColorMode,
  useColorModeValue,
  HStack,
  Link,
} from '@chakra-ui/react';
import { MoonIcon, SunIcon } from '@chakra-ui/icons';
import { Link as RouterLink } from 'react-router-dom';
import CelebrationButton from './CelebrationButton';
import CameraToggleButton from './CameraToggleButton';

function NavBar() {
  const { colorMode, toggleColorMode } = useColorMode();

  const bgColor = useColorModeValue('rgba(255, 255, 255, 0.9)', 'rgba(26, 32, 44, 0.9)');
  const backdropFilter = 'blur(8px)';
  const borderColor = useColorModeValue('gray.200', 'gray.700');
  const textColor = useColorModeValue('gray.800', 'gray.100');

  return (
    <Box
      position="sticky"
      top="0"
      zIndex="999"
      bg={bgColor}
      backdropFilter={backdropFilter}
      borderBottom="1px solid"
      borderColor={borderColor}
      px={8}
      py={3}
      boxShadow="sm"
    >
      <Flex align="center">
        <Heading size="lg" color="teal.500">
          Chess By Panchatantra
        </Heading>

        <Spacer />

        <HStack spacing={4}>
          <CelebrationButton />
          <CameraToggleButton />

          <IconButton
            onClick={toggleColorMode}
            icon={colorMode === 'light' ? <MoonIcon /> : <SunIcon />}
            variant="ghost"
            aria-label="Toggle color mode"
            color={textColor}
            _hover={{ bg: useColorModeValue('gray.200', 'gray.600') }}
          />

          <Link
            as={RouterLink}
            to="/api/players"
            color="teal.500"
            fontSize="lg"
            _hover={{ textDecoration: 'underline' }}
          >
            Players
          </Link>

          {/* The link for "Game Analysis" has been removed from here */}

          <Link
            as={RouterLink}
            to="/"
            color="teal.500"
            fontSize="lg"
            _hover={{ textDecoration: 'underline' }}
          >
            Modules
          </Link>
        </HStack>
      </Flex>
    </Box>
  );
}

export default NavBar;